import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Form, Row, Col, Button, Table } from 'react-bootstrap';

function Summary() {
  const navigate = useNavigate();
  const [period, endDate, amount] = JSON.parse(localStorage.getItem('price'));
  const name = localStorage.getItem('name');
  const pnum = localStorage.getItem('pnum');
  const car = localStorage.getItem('car');
  const car_year = localStorage.getItem('car_year');

  const handlePersonalInfoEdit = () => {
    navigate('/v2/step1');
  };

  const handleProductInfoEdit = () => {
    navigate('/v2/step2');
  };

  const handleNextPage = () => {
    navigate('/v2/step3');
  };

  return (
    <Form
      name="signup"
      id="signup"
      className="needs-validation d-flex flex-column justify-content-center"
      noValidate
      style={{ minHeight: '100vh' }}
    >
      <Row className="align-items-center mb-4">
        <Col className="d-flex justify-content-center">
          <p className="h3 mb-0 text-center">가입정보 확인</p>
        </Col>
      </Row>
      <Row className="align-items-center mb-4">
        <Col className="d-flex justify-content-center">
          <p className="mb-0 text-center">
            아래 정보를 다시 한번 확인해주세요.
          </p>
        </Col>
      </Row>
      <Table striped bordered hover className="align-items-center mb-4">
        <tbody>
          <tr>
            <th colSpan={2} style={{ textAlign: 'center' }}>
              개인정보
            </th>
          </tr>
          <tr>
            <td>이름</td>
            <td>{name}</td>
          </tr>
          <tr>
            <td>생년월일</td>
            <td>{pnum.slice(0, 6)}</td>
          </tr>
          <tr>
            <td>차종</td>
            <td>{car}</td>
          </tr>
          <tr>
            <td>연식</td>
            <td>{car_year}</td>
          </tr>
          <tr>
            <th colSpan={2} style={{ textAlign: 'center' }}>
              상품정보
            </th>
          </tr>
          <tr>
            <td>가입기간</td>
            <td>{period}</td>
          </tr>
          <tr>
            <td>종료일</td>
            <td>{endDate}</td>
          </tr>
          <tr>
            <td>보험료</td>
            <td>{amount}</td>
          </tr>
        </tbody>
      </Table>
      <Row className="justify-content-center">
        <Col md={6} xs={12} className="mb-3 d-flex justify-content-center">
          <Button
            type="submit"
            size="sm"
            style={{
              backgroundColor: '#FFA500', // kb 색상
              border: 'none', // 테두리 제거
              color: 'black',
              fontSize: '12px',
            }}
            onClick={handlePersonalInfoEdit}
          >
            개인정보 수정
          </Button>
          <Button
            size="sm"
            type="submit"
            style={{
              backgroundColor: '#FFA500', // kb 색상
              border: 'none', // 테두리 제거
              marginLeft: '10px', // add margin to the left
              marginRight: '10px', // add margin to the right
              color: 'black',
              fontSize: '12px',
            }}
            onClick={handleProductInfoEdit}
          >
            상품정보 수정
          </Button>
          <Button
            size="sm"
            type="submit"
            style={{
              backgroundColor: '#FFA500', // kb 색상
              border: 'none', // 테두리 제거
              color: 'black',
              fontSize: '12px',
            }}
            onClick={handleNextPage}
          >
            서명 / 가입
          </Button>
        </Col>
      </Row>
    </Form>
  );
}

export default Summary;
