import React from 'react';
import { Row, Col, Form, Button } from 'react-bootstrap';
import SignatureCanvas from 'react-signature-canvas';
import { useNavigate } from 'react-router-dom';

const Step3 = () => {
  const navigate = useNavigate();

  const handleSubmit = (event) => {
    navigate('/v2/step4');
  };

  return (
    <Form
      name="signup"
      id="signup"
      className="needs-validation d-flex flex-column justify-content-center"
      noValidate
      onSubmit={handleSubmit}
      style={{ minHeight: '100vh' }}
    >
      <Row className="align-items-center mb-4">
        <Col md={6} className="d-flex justify-content-center">
          <p className="h3 mb-0 text-center">보험계약 체결 및 이행 동의</p>
        </Col>
      </Row>
      <Row className="align-items-center mb-4">
        <Col md={6} className="d-flex justify-content-center">
          <SignatureCanvas
            penColor="black"
            canvasProps={{
              width: 300,
              height: 100,
              className: 'sigCanvas',
              style: {
                border: '1px solid #ccc',
                borderRadius: '5px',
                boxShadow: '2px 2px 2px #ccc',
              },
            }}
          />
        </Col>
      </Row>
      <Row className="justify-content-start">
        <Col md={6}>
          <ul className="text-muted">
            <li>
              계약 체결을 위한 법률 및 규정에 따라 고객님의 서명이 필요합니다.
            </li>
            <li>
              <strong>
                {' '}
                보험 가입 이외의 다른 용도로 절대 사용하지 않습니다.
              </strong>
            </li>
          </ul>
        </Col>
      </Row>
      <Row className="justify-content-end">
        <Col md={6} xs={12} className="mb-3 d-flex justify-content-end">
          <Button variant="warning" size="sm" type="submit">
            서명
          </Button>
        </Col>
      </Row>
      <Row className="align-items-center mb-4"></Row>
    </Form>
  );
};

export default Step3;
